import mongoose from "mongoose";

const DocumentChunkSchema = new mongoose.Schema({
  content: { type: String, required: true },
  embedding: { type: [Number], required: true },
  metadata: {
    source: { type: String, required: true },
    page: { type: Number },
  },
});

// Création de l'index vectoriel
DocumentChunkSchema.index({ embedding: "cosmosSearch" }, { cosmosSearchOptions: { kind: "vector-ivf", numLists: 1, similarity: "COS" } });

export default mongoose.models.DocumentChunk || mongoose.model("DocumentChunk", DocumentChunkSchema);