import { NextResponse } from "next/server";
import { MongoVectorStore } from "@/services/rag/vectorStore";
import { getAuth } from "@clerk/nextjs/server";

export const maxDuration = 600; // 10 minutes

export async function POST(req) {
  try {
    const { userId } = getAuth(req);
    if (!userId) {
      return NextResponse.json(
        { success: false, message: "Authentication required" },
        { status: 401 }
      );
    }

    const { prompt } = await req.json();
    if (!prompt) {
      return NextResponse.json(
        { success: false, message: "Prompt is required" },
        { status: 400 }
      );
    }

    const vectorStore = new MongoVectorStore();
    const relevantChunks = await vectorStore.similaritySearch(prompt);

    return NextResponse.json({
      success: true,
      data: {
        role: "assistant",
        content: "Contexte RAG récupéré avec succès",
        context: relevantChunks,
        timestamp: Date.now(),
      },
    });
  } catch (error) {
    console.error("RAG Error:", error);
    return NextResponse.json(
      { success: false, message: "RAG processing failed", error: error.message },
      { status: 500 }
    );
  }
}